import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
// import { setShiftSchool } from 'dump/data';
import { getStudySessions } from 'services/studySession';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={5}>
            <Form.Item label="Ca học:">
              {getFieldDecorator('studySessionId', {})(<SelectAjax service={getStudySessions} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommon;



