import { request } from 'utils';

const prefix = '/api/education';

export const getClassAllocations = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studySessionId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassAllocation',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studySessionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const createClassAllocations = data =>
  request({
    prefix,
    url: '/ClassAllocation',
    method: 'POST',
    params: {},
    data
  });


export const autoAllocateClass = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId
} = {}) =>
  request({
    prefix,
    url: '/ClassAllocation/AllocateAuto',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId
    }
  });
  



