import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoCommon from './FormInfoCommon';
import TableClassList from './TableClassList';
import './style.scss';

class ClassListPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh sách lớp học">
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <TableClassList />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassListPage />
  </Page>
);



