import React, { Component, Fragment } from 'react';
import { Input, Form, message, Row, Col } from 'antd';
import * as _ from 'lodash';
import { TableDataEditCell, SelectAjax } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { BtnSave, Btn } from 'components/common/button';
import { getClassAllocations, createClassAllocations, autoAllocateClass } from 'services/classAllocation';
import { getStudySessions } from 'services/studySession';
import { getBuildingRoomForSelect } from 'services/buildingRoom';

class TableClassList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      // loading: false,
      paramSearch: {},
      editedClasses: {},
      selectedRowKeys: [],
      isDivideBtnEnable: false,
    };
    this.columns = [
      {
        title: 'Lớp',
        dataIndex: 'classLabel',
        key: 'classLabel',
      },
      {
        title: 'Số SV',
        dataIndex: 'noOfStudent',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.classId}.noOfStudent`, {
              initialValue: record.noOfStudent,
              // rules: [requiredRule],
            })(
              <Input
                onPressEnter={onSave}
                onBlur={onSave}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Ca học dự kiến',
        dataIndex: 'studySessionId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.classId}.studySessionId`, {
              initialValue: record.studySessionId,
              // rules: [requiredRule],
            })(
              <SelectAjax
                form={form}
                service={getStudySessions}
                allowGetObjSelected
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Phòng học',
        dataIndex: 'roomId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.classId}.roomId`, {
              initialValue: record.roomId,
              // rules: [requiredRule],
            })(
              <SelectAjax
                form={form}
                service={getBuildingRoomForSelect}
                allowGetObjSelected
                onPressEnter={onSave}
                onBlur={onSave}
                ref={setRefFormItem}
                keyValue="value"
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Hệ đào tạo',
        dataIndex: 'trainType',
        key: 'trainType',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'faculty',
        key: 'faculty',
      },
      {
        title: 'Khóa học',
        dataIndex: 'course',
        key: 'course',
      },
      {
        title: 'Tên ngành',
        dataIndex: 'studyField',
        key: 'studyField',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  handleAutoAllocate = async() => {
    await autoAllocateClass(this.state.paramSearch);
    message.success('Phân bổ lớp tự động thành công');
    this.setState({ reload: this.state.reload + 1 });
  }

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSave = async() => {
    try {
      const { selectedRowKeys, editedClasses } = this.state;
      const savingClasses = Object.entries(editedClasses)
        .filter(classKeyValue => selectedRowKeys.includes(Number(classKeyValue[0])))
        .map(classKeyValue => classKeyValue[1]);
      await createClassAllocations(Object.values(savingClasses));
      message.success('Lưu thành công');
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleSaveCellData = async (rowData) => {
    this.setState({
      editedClasses: {...this.state.editedClasses, [rowData.classId]: rowData }
    });
  };

  toggleEnableDivideRoomIfNeeded = data => {
    this.setState({ isDivideBtnEnable: data && !_.isEmpty(data.items) })
  }

  render() {
    const { reload, paramSearch, isDivideBtnEnable, selectedRowKeys } = this.state;
    const { trainTypeId, facultyId } = paramSearch;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    console.log(this.state.editedClasses)
    return (
      <Fragment>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSave onClick={this.handleSave} disabled={_.isEmpty(selectedRowKeys)} />
              <Btn title="Phân phòng tự động" disabled={!isDivideBtnEnable} onClick={this.handleAutoAllocate} />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditCell
              reload={reload}
              paramSearch={paramSearch}
              service={async params => {
                const result = await getClassAllocations(params);
                this.toggleEnableDivideRoomIfNeeded(result.data);
                return result;
              }}
              columns={this.columns}
              handleSaveCellData={this.handleSaveCellData}
              allowGetData={!!trainTypeId && !!facultyId}
              rowSelection={rowSelection}
              rowKey="classId"
            />
          </Col>
        </Row>
      </Fragment>
    );
  }
}

export default TableClassList;



